import { Component, ViewEncapsulation } from '@angular/core';
import { AlertHolderDirective } from '../../../bedag-components/alert/alert-holder.directive';

@Component({
  selector: 'app-layout',
  templateUrl: './overflow-menu.component.html',
  encapsulation: ViewEncapsulation.None,
})
export class OverflowMenuComponent {
  constructor(private readonly alerts: AlertHolderDirective) {}

  showAlert(alertText: string): void {
    this.alerts.success(`${alertText} wurde ausgewählt`);
  }
}
