import { Component, OnInit, ViewEncapsulation } from '@angular/core';
import { DemoStatus } from '../../../demo-utils/status/demo-status.directive';

@Component({
  selector: 'app-form-examples',
  templateUrl: './small-form-example.component.html',
  encapsulation: ViewEncapsulation.None,
})
export class SmallFormExampleComponent implements OnInit {
  issue = {
    subject: null,
  };
  demoStatus = DemoStatus;
  date?: '2004-06-02';
  status = this.demoStatus.ok;
  invalid = false;

  ngOnInit(): void {
    this.switchStatus(this.demoStatus.ok);
  }

  switchStatus(s: DemoStatus): void {
    this.status = s;
    this.invalid = this.status === DemoStatus.invalid;
  }
}
