export default "import { Component, Input, OnInit } from '@angular/core';\nimport { SourceCodeService } from '../../source-code.service';\n\nimport * as Prism from 'prismjs';\nimport 'prismjs/components/prism-typescript';\n\n@Component({\n  selector: 'app-code-example',\n  templateUrl: 'code-example.component.html',\n})\nexport class CodeExampleComponent implements OnInit {\n  static STASH_URL =\n    'https://stash.se.bedag.ch/projects/ENG-LIBS/repos/bedag-angular-styleguide/browse/frontend/src/app/styleguide';\n\n  /**\n   * Base path for the src file names, relative to src/app/styleguide.\n   */\n  @Input()\n  basePath?: string;\n  /**\n   * A comma separated list of source file names to display.\n   * Html file names may contain an anchor, to only display the content of the code-example with\n   * that anchor (rather than the entire file)\n   */\n  @Input()\n  src!: string;\n\n  /**\n   * An optional delimiter. This enables wrapping the code you want to display\n   * with line comments containing '<delimiter> start' and '<delimiter> end'.\n   *\n   * @example\n   *   <!-- code example start -->\n   *     <!-- this code is rendered when showCode is true-->\n   *   <!-- code example end -->\n   *   <app-code-example src=\"src.html\" delimiter=\"code example\">\n   *     Text describing the code example\n   *   </app-code-example>\n   */\n  @Input()\n  delimiter?: string;\n\n  showCode = false;\n  files!: {\n    prettyName: string;\n    path: string;\n    language: string;\n    code: string;\n    highlightedCode?: string;\n  }[];\n\n  constructor(private readonly sourceCodeService: SourceCodeService) {}\n\n  ngOnInit(): void {\n    this.files = this.src.split(',').map((fileNameWithAnchor) => {\n      const [fileName, anchor] = fileNameWithAnchor.split('#');\n      const language = fileName.substring(fileName.lastIndexOf('.') + 1);\n      const prettyName = fileName.substring(fileName.lastIndexOf('/') + 1);\n      const path = this.basePath ? this.basePath.concat('/', fileName) : fileName;\n      let code = this.sourceCodeService.sourceCode(path);\n\n      if (anchor) {\n        let idx = code.indexOf(fileNameWithAnchor);\n        if (idx === -1) {\n          throw new Error(\n            `The source file ${fileName} does not contain the marker ${fileNameWithAnchor}`\n          );\n        } else if (code.indexOf(fileNameWithAnchor, idx + 1) !== -1) {\n          throw new Error(\n            `The source file ${fileName} contains the marker ${fileNameWithAnchor} more than once. Please specify a unique marker`\n          );\n        }\n        idx = code.indexOf('\\n', idx) + 3;\n\n        const closingTagIdx = code.indexOf('</app-code-example>', idx);\n        code = code.substring(idx, closingTagIdx);\n        code = code.split('\\n  ').join('\\n');\n      } else if (this.delimiter) {\n        let lines = code.split('\\n');\n        const startIdx = lines.findIndex((line) =>\n          line.includes(`${this.delimiter as string} start`)\n        );\n        const endIdx = lines.findIndex((line) => line.includes(`${this.delimiter as string} end`));\n        lines = lines.slice(startIdx + 1, endIdx);\n        code = lines.join('\\n');\n      }\n      return { prettyName, path, language, code };\n    });\n  }\n\n  showOrHide(): void {\n    this.showCode = !this.showCode;\n    if (this.showCode) {\n      for (const file of this.files) {\n        file.highlightedCode =\n          file.highlightedCode ||\n          Prism.highlight(file.code, Prism.languages[file.language], file.language);\n      }\n    }\n  }\n\n  getStashUrl(): string {\n    if (this.basePath) {\n      return CodeExampleComponent.STASH_URL.concat('/', this.basePath);\n    } else {\n      const firstFile = this.files[0];\n      if (this.files.length === 1) {\n        return CodeExampleComponent.STASH_URL.concat('/', this.files[0].path);\n      }\n      const dir = firstFile.path.substring(0, firstFile.path.lastIndexOf('/') + 1);\n      return CodeExampleComponent.STASH_URL.concat('/', dir);\n    }\n  }\n}\n";