import { interval as observableInterval, Observable } from 'rxjs';

import { map, startWith, take } from 'rxjs/operators';
import { Component } from '@angular/core';

@Component({
  selector: 'app-loader',
  templateUrl: './progress-indicators.component.html',
})
export class ProgressIndicatorsComponent {
  progress!: Observable<number>;
  progressWithETA!: Observable<number>;
  progressETA!: Observable<string>;
  jobRunning = false;
  job2Running = false;
  isLoading = false;
  isSaving = false;

  longRunningAction(): void {
    this.progress = observableInterval(1000).pipe(
      take(10),
      map((i) => 10 * (i + 1))
    );
  }

  longRunningActionWithETA(): void {
    const seconds = 30;
    const observable = observableInterval(1000).pipe(
      startWith(-1),
      take(seconds + 1),
      map((i) => i + 1)
    );
    this.progressWithETA = observable.pipe(map((i) => (100 * i) / seconds));
    this.progressETA = observable.pipe(
      map((i) => (i < seconds ? `${seconds - i}s verbleiben` : 'Fertig!'))
    );
  }

  startAsyncJob(job: string): void {
    if (job === 'job1') {
      this.jobRunning = true;
      setTimeout(() => (this.jobRunning = false), 3000);
    } else if (job === 'job2') {
      this.job2Running = true;
      setTimeout(() => (this.job2Running = false), 3000);
    }
  }

  showLoadingOverlay(): void {
    this.isLoading = true;
    setTimeout(() => (this.isLoading = false), 4000);
  }

  showSavingOverlay(): void {
    this.isSaving = true;
    setTimeout(() => (this.isSaving = false), 4000);
  }
}
