export default "import { Component, OnInit, ViewEncapsulation } from '@angular/core';\nimport { ADDRESS_DATA, AddressData } from './data';\nimport { NgbModal, NgbModalRef } from '@ng-bootstrap/ng-bootstrap';\nimport { AlertHolderDirective } from '../../../bedag-components/alert/alert-holder.directive';\nimport { CustomMaxWidthComponent } from '../../../bedag-components/layout/custom-max-width-component';\n\n@Component({\n  selector: 'app-tables',\n  templateUrl: './datatables.component.html',\n  encapsulation: ViewEncapsulation.None,\n})\nexport class DatatablesComponent implements OnInit, CustomMaxWidthComponent {\n  maxWidth = '100%';\n\n  addressData: Array<AddressData> = [];\n  pagedItems: Array<AddressData> = [];\n  selected: number[] = [];\n  sortedBy!: string;\n  sortedAscending!: boolean;\n  searchTerm!: string;\n  page = 1;\n  pageSize = 5;\n  pageSizes = [5, 10, 25, 100];\n  modal!: NgbModalRef;\n  selectAll = false;\n  selectedForDetail!: AddressData;\n\n  constructor(\n    private readonly modalService: NgbModal,\n    private readonly alerts: AlertHolderDirective\n  ) {}\n\n  ngOnInit(): void {\n    this.addressData = ADDRESS_DATA;\n    this.sortBy('name');\n  }\n\n  getColumnClassNames(column: string): string {\n    let classNames = 'fas fa-sort';\n    if (this.sortedBy === column) {\n      classNames += this.sortedAscending ? '-up' : '-down';\n      classNames += ' text-primary';\n    }\n    return classNames;\n  }\n\n  /**\n   * This function could easily be replaced by the sortBy or orderBy functions from underscore or\n   * lodash, but we don't want to force the dependency on the users.\n   */\n  private sortAddressData(): void {\n    this.addressData.sort((addressDataA, addressDataB) => {\n      if (addressDataA[this.sortedBy] < addressDataB[this.sortedBy]) {\n        return -1;\n      } else {\n        return addressDataA[this.sortedBy] > addressDataB[this.sortedBy] ? 1 : 0;\n      }\n    });\n  }\n\n  sortBy(field: string): boolean {\n    if (this.sortedBy === field) {\n      this.sortedAscending = !this.sortedAscending;\n      this.addressData.reverse();\n    } else {\n      this.sortedBy = field;\n      this.sortedAscending = true;\n      this.sortAddressData();\n    }\n\n    this.setPage(1);\n    return false; // do not follow the href\n  }\n\n  search(): void {\n    this.addressData = ADDRESS_DATA.filter(\n      (item) => JSON.stringify(item).toLowerCase().indexOf(this.searchTerm.toLowerCase()) !== -1\n    );\n\n    if (this.sortedBy) {\n      this.sortAddressData();\n\n      if (!this.sortedAscending) {\n        this.addressData.reverse();\n      }\n    }\n\n    this.setPage(1);\n  }\n\n  setPage(page: number): void {\n    this.page = page;\n    const startIndex = (this.page - 1) * this.pageSize;\n    const endIndex = Math.min(startIndex + this.pageSize, this.addressData.length);\n    this.pagedItems = this.addressData.slice(startIndex, endIndex);\n    this.selected = [];\n    this.selectAll = false;\n  }\n\n  showModal(content: unknown, index: number): boolean {\n    this.selectedForDetail = this.pagedItems[index];\n    this.modal = this.modalService.open(content);\n    return false;\n  }\n\n  isSelected(i: number): boolean {\n    return this.selectAll || this.selected.includes(i);\n  }\n\n  toggleSelection(i: number): void {\n    if (this.selected.includes(i)) {\n      this.selected = this.selected.filter((n) => n !== i);\n    } else {\n      this.selected.push(i);\n    }\n  }\n\n  toggleSelectAll(): void {\n    this.selectAll = !this.selectAll;\n    if (!this.selectAll) {\n      this.selected = [];\n    }\n  }\n\n  focusNextCheckbox(event: KeyboardEvent, i: number): void {\n    switch (event.key) {\n      case 'ArrowDown':\n        document.getElementById(`checkbox_${i + 1}`)?.focus();\n        break;\n      case 'ArrowUp':\n        document.getElementById(`checkbox_${i - 1}`)?.focus();\n        break;\n    }\n  }\n\n  showAlert(address: AddressData, alertText: string): boolean {\n    this.alerts.success(`${alertText} wurde ausgewählt für ${address.vorname} ${address.name}`);\n    return false;\n  }\n\n  processSelected(action: string): boolean {\n    if (!this.selectAll && this.selected.length < 1) {\n      this.alerts.success('Keine Einträge ausgewählt');\n    } else {\n      let text = `${action} `;\n      if (this.selectAll) {\n        text += this.pagedItems.map((a) => `${a.vorname} ${a.name}`).join(', ');\n      } else {\n        text += this.selected\n          .map((i) => `${this.pagedItems[i].vorname} ${this.pagedItems[i].name}`)\n          .join(', ');\n      }\n      this.alerts.success(text);\n    }\n    return false;\n  }\n}\n";