export default "import { Component, EventEmitter, Input, Output } from '@angular/core';\nimport { LanguageService } from '../i18n/language.service';\n\n@Component({\n  selector: 'app-alert',\n  template: `\n    <div\n      class=\"alert fade alert-{{ type }} clearfix\"\n      [class.alert-dismissible]=\"dismissible\"\n      [class.show]=\"!fading\"\n      role=\"alert\">\n      <button\n        type=\"button\"\n        class=\"close\"\n        [attr.aria-label]=\"closeButtonLabels[this.languageService.currentLanguage]\"\n        (click)=\"fading = true; dismiss.emit()\"\n        *ngIf=\"dismissible\">\n        <span aria-hidden=\"true\">&times;</span>\n      </button>\n      <span class=\"fas {{ icons[type] }} mr-2\"></span>\n      <ng-content></ng-content>\n    </div>\n  `,\n})\nexport class AlertComponent {\n  @Input()\n  type!: string;\n\n  @Output()\n  dismiss = new EventEmitter<void>();\n\n  constructor(readonly languageService: LanguageService) {}\n\n  get dismissible(): boolean {\n    return this.dismiss.observed;\n  }\n\n  fading = false;\n\n  icons: { [index: string]: string } = {\n    danger: 'fa-exclamation-circle',\n    warning: 'fa-exclamation-triangle',\n    success: 'fa-check-circle',\n    info: 'fa-info-circle',\n  };\n\n  closeButtonLabels: { [index: string]: string } = {\n    en: 'Close',\n    de: 'Schliessen',\n    fr: 'Fermer',\n    it: 'Chiudere',\n  };\n}\n";