export default "import { Component } from '@angular/core';\n\n@Component({\n  templateUrl: 'functionbar-demo.component.html',\n})\nexport class FunctionbarDemoComponent {\n  print(): boolean {\n    window.print();\n    return false;\n  }\n\n  reload(): boolean {\n    window.location.reload();\n    return false;\n  }\n\n  noop(): boolean {\n    // We shouldn't have menu entries that do nothing, this is just to keep the demo simple.\n    // It's important to return `false` in these methods so the application will not follow\n    // the link and reload the page.\n    return false;\n  }\n}\n";