// and expose it as NgbDatepicker wants
@Injectable()
export class DatepickerI18n extends NgbDatepickerI18n {
  constructor(private readonly languageService: LanguageService) {
    super();
  }

  getWeekdayLabel(weekday: number, width?: TranslationWidth): string {
    if (width === TranslationWidth.Wide) {
      return languages[this.languageService.currentLanguage].days[weekday % 7]; // Ngb has Sunday last, bootstrap-datepicker has it first
    }
    return languages[this.languageService.currentLanguage].daysShort[weekday % 7]; // Ngb has Sunday last, bootstrap-datepicker has it first
  }

  getMonthShortName(month: number): string {
    return languages[this.languageService.currentLanguage].monthsShort[month - 1];
  }

  getMonthFullName(month: number): string {
    return languages[this.languageService.currentLanguage].months[month - 1];
  }

  getDayAriaLabel(date: NgbDateStruct): string {
    const lang = this.languageService.currentLanguage;
    const month = languages[lang].months[date.month - 1];
    switch (lang) {
      case 'de':
        return `${date.day}. ${month} ${date.year}`;
      case 'en':
        return `${month} ${date.day} ${date.year}`;
      default:
        return `${date.day}. ${month} ${date.year}`;
    }
  }
}
