export default "import { Component } from '@angular/core';\nimport { AlertHolderDirective } from './alert-holder.directive';\n\n/**\n * Displays the alerts of the enclosing AlertHolder.\n */\n@Component({\n  selector: 'app-alert-list',\n  template: `\n    <app-alert\n      *ngFor=\"let alert of alertHolder.alerts\"\n      [type]=\"alert.type\"\n      (dismiss)=\"alertHolder.dismiss(alert)\">\n      {{ alert.message }}\n    </app-alert>\n  `,\n})\nexport class AlertListComponent {\n  constructor(public alertHolder: AlertHolderDirective) {}\n}\n";