import { Component, EventEmitter, Input, Output, ViewEncapsulation } from '@angular/core';
import { Router } from '@angular/router';

@Component({
  selector: 'app-functionbar-item',
  template: `
    <li class="nav-item">
      <div ngbTooltip="{{ tooltip }}" placement="bottom">
        <button
          type="button"
          class="btn btn-light btn-functionbar btn-lg nav-item"
          [attr.aria-label]="label ? null : tooltip"
          [disabled]="disabled"
          (click)="onclick($event)">
          <span *ngIf="icon" class="{{ icon }} fa-fw" aria-hidden="true"></span>
          <small *ngIf="label">{{ label }}</small>
        </button>
      </div>
    </li>
  `,
  encapsulation: ViewEncapsulation.None,
})
export class FunctionbarItemComponent {
  @Input() routerLink!: string;
  @Input() icon!: string;
  @Input() tooltip!: string;
  @Input() label!: string;

  @Input() disabled = false;

  @Output() itemClicked = new EventEmitter<void>();

  constructor(private readonly router: Router) {}

  onclick(event: MouseEvent): void {
    this.itemClicked.emit();
    if (this.routerLink) {
      void this.router.navigate([this.routerLink]);
    }
    event.stopPropagation();
    // the (click) subscribers have also subscribed to the DOM event, and we don't want them to be invoked twice ...
  }
}
