import { Injectable } from '@angular/core';
import { SourceCodeService } from './source-code.service';

const context = require.context('!!raw-loader!../', true, /.(component|data).(html|ts)$/);

@Injectable({
  providedIn: 'root',
})
export class WebpackSourceCodeService implements SourceCodeService {
  sourceCode(filename: string): string {
    const key = `./styleguide/${filename}`.replace('styleguide/../', '');
    // eslint-disable-next-line @typescript-eslint/no-unsafe-member-access,@typescript-eslint/no-unsafe-return
    return context(key).default;
  }
}
