export default "import { Component, ViewEncapsulation } from '@angular/core';\nimport { Observable, OperatorFunction } from 'rxjs';\nimport { debounceTime, distinctUntilChanged, filter, map } from 'rxjs/operators';\n\nimport { DATA } from './data';\n\nconst MAX_SEARCH_RESULTS = 10;\n\n@Component({\n  selector: 'app-buttons',\n  templateUrl: './autocompletion-demo.component.html',\n  encapsulation: ViewEncapsulation.None,\n})\nexport class AutocompletionDemoComponent {\n  private readonly countries: string[] = DATA.filter((t) => t.type === 'Land').map(\n    (t) => t.shortNameDe\n  );\n  private readonly data: Item[] = DATA.map((item) => new Item(item.type, item.shortNameDe));\n  public country!: string;\n\n  public item!: Item;\n\n  /** EXAMPLE Simple search method\n   * Results that start with the exact search term will be shown first.\n   * This search algorithm is suited for data sets consisting of single words.\n   * For a more complete algorithm see below.\n   */\n  searchCountries: OperatorFunction<string, readonly string[]> = (text$: Observable<string>) =>\n    text$.pipe(\n      debounceTime(200),\n      distinctUntilChanged(),\n      filter((term) => term.length > 0),\n      map((term) => {\n        const normalizedTerm = term.toLowerCase().trim();\n        const startsWith = this.countries\n          .filter((c) => c.toLowerCase().startsWith(normalizedTerm))\n          .slice(0, MAX_SEARCH_RESULTS);\n        const remaining = MAX_SEARCH_RESULTS - startsWith.length;\n        if (remaining === 0) {\n          return startsWith;\n        }\n        const contains = this.countries\n          .filter((c) => c.toLowerCase().includes(normalizedTerm, 1))\n          .slice(0, remaining);\n        return startsWith.concat(contains);\n      })\n    );\n\n  /** EXAMPLE Complex search method\n   * This search algorithm is suited for data sets consisting of multiple words.\n   * 1st priority: Results that start with the exact search term.\n   * 2nd priority: Results that contain a word, starting with the exact search term.\n   * 3rd priority: Results containing the search term anywhere.\n   */\n  searchLocation: OperatorFunction<string, readonly Item[]> = (text$: Observable<string>) =>\n    text$.pipe(\n      debounceTime(200),\n      distinctUntilChanged(),\n      filter((term) => term.length > 0),\n      map((term) => {\n        const normalizedTerm = term.toLowerCase().trim();\n        let result = this.data\n          .filter((c) => c.startsWith(normalizedTerm))\n          .slice(0, MAX_SEARCH_RESULTS);\n        let remaining = MAX_SEARCH_RESULTS - result.length;\n\n        if (remaining > 0) {\n          const tokenMatches = this.data\n            .filter((c) => !result.includes(c))\n            .filter((c) => c.tokenMatch(normalizedTerm))\n            .slice(0, MAX_SEARCH_RESULTS);\n          result = result.concat(tokenMatches);\n          remaining = MAX_SEARCH_RESULTS - result.length;\n        }\n\n        if (remaining > 0) {\n          const contains = this.data\n            .filter((c) => !result.includes(c))\n            .filter((c) => c.matches(normalizedTerm))\n            .slice(0, remaining);\n          result = result.concat(contains);\n        }\n        return result;\n      })\n    );\n\n  formatItem(item: Item): string {\n    return `${item.shortNameDe}`;\n  }\n}\n\nclass Item {\n  constructor(public type: string, public shortNameDe: string) {}\n\n  public startsWith(term: string): boolean {\n    return this.shortNameDe.toLowerCase().startsWith(term);\n  }\n\n  public tokenMatch(term: string): boolean {\n    const terms = term.split(/\\s+/);\n    const tokens = this.shortNameDe.toLowerCase().split(/\\s+/);\n    let tokenIndex = -1;\n    for (const termToken of terms) {\n      let found = false;\n      for (let i = tokenIndex + 1; i < tokens.length; i++) {\n        if (tokens[i].startsWith(termToken)) {\n          tokenIndex = i;\n          found = true;\n          break;\n        }\n      }\n      if (!found) {\n        return false;\n      }\n    }\n    return true;\n  }\n\n  public matches(term: string): boolean {\n    return this.shortNameDe.toLowerCase().indexOf(term) >= 0;\n  }\n}\n";