import { Component, HostBinding, Input } from '@angular/core';

@Component({
  selector: 'app-functionbar-list',
  template: ` <ng-content></ng-content> `,
})
export class FunctionbarListComponent {
  @Input()
  align: 'left' | 'right' = 'left';

  @HostBinding('class') class = 'functionbarlist navbar-nav';

  @HostBinding('class.ml-auto') get alignment(): boolean {
    return this.align === 'right';
  }
}
