export default "import { Component, forwardRef, HostBinding, Input, ViewChild } from '@angular/core';\nimport {\n  NgbCalendar,\n  NgbDateAdapter,\n  NgbDateParserFormatter,\n  NgbDatepickerI18n,\n  NgbInputDatepicker,\n} from '@ng-bootstrap/ng-bootstrap';\nimport { ControlValueAccessor, NG_VALUE_ACCESSOR } from '@angular/forms';\nimport { DatepickerI18n } from './datepicker.i18n';\nimport { SwissDateParserFormatter } from './swiss-date-parser-formatter';\nimport { IsoDateAdapter } from './iso-date-adapter';\n\n/**\n * @typeParam T - defines the type used by the backend. As long as you use the IsoDateAdapter, this will be <string>.\n */\n@Component({\n  selector: 'app-datepicker',\n  templateUrl: './datepicker.component.html',\n  providers: [\n    {\n      provide: NG_VALUE_ACCESSOR,\n      useExisting: forwardRef(() => DatepickerComponent),\n      multi: true,\n    },\n    {\n      provide: NgbDateAdapter,\n      useClass: IsoDateAdapter,\n    },\n    {\n      provide: NgbDateParserFormatter,\n      useClass: SwissDateParserFormatter,\n    },\n    {\n      provide: NgbDatepickerI18n,\n      useClass: DatepickerI18n,\n    },\n  ],\n})\nexport class DatepickerComponent<T> implements ControlValueAccessor {\n  @HostBinding('attr.id')\n  externalId: string | null = '';\n\n  @ViewChild(NgbInputDatepicker, { static: true })\n  datepicker!: NgbInputDatepicker;\n\n  @ViewChild('dateButton')\n  button!: HTMLButtonElement;\n\n  private _value!: T | null;\n\n  public get value(): T | null {\n    return this._value;\n  }\n\n  public set value(v: T | null) {\n    this._value = v;\n    this.onChange(this._value);\n    this.onTouched();\n  }\n\n  private _id = '';\n\n  @Input()\n  set id(value: string) {\n    this._id = value;\n    this.externalId = null;\n  }\n\n  get id(): string {\n    return this._id;\n  }\n\n  constructor(\n    private readonly calendar: NgbCalendar,\n    private readonly adapter: NgbDateAdapter<T>\n  ) {}\n\n  selectToday(): void {\n    const today = this.calendar.getToday();\n    this.value = this.adapter.toModel(today);\n    this.datepicker.navigateTo(today);\n  }\n\n  /**\n   * Empty listener, to be overwritten by 'registerOnChange'\n   */\n  onChange: (date: T | null) => void = () => undefined;\n  /**\n   * Empty listener, to be overwritten by 'registerOnTouched'\n   */\n  onTouched: () => void = () => undefined;\n\n  registerOnChange(fn: (date: T | null) => void): void {\n    this.onChange = fn;\n  }\n\n  registerOnTouched(fn: () => void): void {\n    this.onTouched = fn;\n  }\n\n  setDisabledState(isDisabled: boolean): void {\n    this.datepicker.disabled = isDisabled;\n    this.button.disabled = isDisabled;\n  }\n\n  writeValue(obj: T): void {\n    this.value = obj;\n    this.onChange(this.value);\n  }\n}\n";