import { Component } from '@angular/core';

@Component({
  templateUrl: './header-demo.component.html',
})
export class HeaderDemoComponent {
  navigationToggleText = 'Close navigation';
  languages = ['Deutsch', 'Français', 'English'];
  selectedLanguage = 'Deutsch';
  loggedIn = true;

  toggleNavigation(): void {
    const e = document.body;
    if (e && e.classList) {
      const closed = e.classList.toggle('close-navigation');
      this.navigationToggleText = closed ? 'Open navigation' : 'Close navigation';
    }
  }

  selectLanguage(language: string): void {
    this.selectedLanguage = language;
  }

  toggleLogin(): void {
    this.loggedIn = !this.loggedIn;
  }
}
