import {
  Directive,
  ElementRef,
  Input,
  OnChanges,
  OnInit,
  Renderer2,
  SimpleChanges,
} from '@angular/core';

const ALL_CLASSES = ['is-invalid', 'is-read-only'];

/**
 * Hack Directive, only for demo purposes. Definitely not a best practice :>)
 */
@Directive({ selector: '[appDemoStatus]' })
export class DemoStatusDirective implements OnChanges, OnInit {
  @Input()
  status!: DemoStatus;
  private inputId!: string;

  constructor(
    private readonly element: ElementRef<HTMLInputElement>,
    private readonly r2: Renderer2
  ) {}

  ngOnInit(): void {
    this.inputId = this.element.nativeElement.id;
    setTimeout(() => this.resetStatus(), 1000);
  }

  setDisabled(d: boolean): void {
    this.element.nativeElement.disabled = d;
  }

  addClass(c: string): void {
    this.r2.addClass(this.element.nativeElement, c);
  }

  ngOnChanges(changes: SimpleChanges): void {
    const status = changes.status;
    if (!status) {
      return;
    }

    this.resetStatus();
  }

  private resetStatus() {
    ALL_CLASSES.forEach((a) => {
      this.r2.removeClass(this.element.nativeElement, a);
    });
    let d = false;

    switch (this.status) {
      case DemoStatus.invalid:
        this.addClass('is-invalid');
        break;
      case DemoStatus.readOnly:
        this.addClass('is-read-only');
        d = true;
        break;
      default:
        break;
    }
    this.setDisabled(d);
  }
}

export enum DemoStatus {
  ok = 'ok',
  invalid = 'invalid',
  readOnly = 'readOnly',
}
