export default "import { filter, startWith, takeUntil } from 'rxjs/operators';\nimport {\n  AfterContentInit,\n  Component,\n  ContentChildren,\n  HostBinding,\n  Input,\n  Optional,\n  QueryList,\n  SkipSelf,\n} from '@angular/core';\n\nimport { NavigationEnd, Router } from '@angular/router';\nimport { RxjsSubscriber } from '../utils/rxjs-subscriber';\nimport { NavMenuComponent } from './nav-menu.component';\nimport { NavigationService } from './navigation.service';\n\n@Component({\n  selector: 'app-nav-item',\n  templateUrl: 'nav-item.component.html',\n})\nexport class NavItemComponent extends RxjsSubscriber implements AfterContentInit {\n  @HostBinding('class') class = 'nav-item';\n\n  @Input() target!: string; // specifies the window or tab the link should open in. defaults to the current window/tab.\n  @Input() href!: string; // external links (to other websites)\n  @Input() url!: string; // internal links (within this app)\n\n  @Input() label!: string;\n  @Input() icon!: string;\n  @Input() startExpanded = false;\n\n  @ContentChildren(NavItemComponent, { descendants: false })\n  childNodes!: QueryList<NavItemComponent>;\n\n  children = false;\n\n  level: number;\n\n  isExpanded = false;\n  isSelected = false;\n  classes!: { [index: string]: boolean };\n\n  constructor(\n    private readonly navigation: NavMenuComponent,\n    private readonly router: Router,\n    @Optional() @SkipSelf() public readonly parent: NavItemComponent,\n    private readonly navigationService: NavigationService\n  ) {\n    super();\n    this.level = (parent ? parent.level : 0) + 1;\n  }\n\n  ngAfterContentInit(): void {\n    this.childNodes.changes.pipe(startWith('')).subscribe(() => {\n      this.children = this.childNodes.some((x) => x !== this); // the content query also matches `this` ...\n    });\n\n    this.router.events\n      .pipe(\n        filter((e) => e instanceof NavigationEnd),\n        startWith(null as unknown),\n        takeUntil(this.destroyed)\n      )\n      .subscribe(() => {\n        this.isSelected =\n          !!this.url &&\n          this.router.isActive(this.url, {\n            paths: 'exact',\n            queryParams: 'exact',\n            fragment: 'ignored',\n            matrixParams: 'ignored',\n          });\n        this.isExpanded =\n          this.startExpanded ||\n          this.isExpanded ||\n          (!!this.url &&\n            this.router.isActive(this.url, {\n              paths: 'subset',\n              queryParams: 'subset',\n              fragment: 'ignored',\n              matrixParams: 'ignored',\n            })) ||\n          this.hasSelectedChild();\n        this.updateClasses();\n      });\n  }\n\n  toggleExpanded(event: Event | null): boolean {\n    this.isExpanded = !this.isExpanded;\n    this.startExpanded = false;\n    this.updateClasses();\n    if (event != null) {\n      event.stopPropagation();\n    }\n    return false;\n  }\n\n  click(): boolean {\n    if (this.children) {\n      this.toggleExpanded(null);\n      if (\n        this.navigationService.isMobile() &&\n        this.url &&\n        !this.router.isActive(this.url, {\n          paths: 'subset',\n          queryParams: 'subset',\n          fragment: 'ignored',\n          matrixParams: 'ignored',\n        })\n      ) {\n        this.navigationService.toggleNavigation();\n        return true;\n      } else {\n        return false;\n      }\n    } else {\n      if (this.navigationService.isMobile()) {\n        this.navigationService.toggleNavigation();\n      }\n      return true;\n    }\n  }\n\n  updateClasses(): void {\n    this.classes = {\n      [`level-${this.level}`]: true,\n      collapsed: !this.isExpanded,\n      expanded: this.isExpanded,\n      selected: this.isSelected,\n    };\n  }\n\n  hasSelectedChild(): boolean {\n    return (\n      !!this.childNodes &&\n      (this.childNodes.some((c) => c.isSelected) ||\n        this.childNodes.some((c) => c !== this && c.hasSelectedChild()))\n    );\n  }\n}\n";